import React from 'react';
import {FaThumbsUp, FaMap} from 'react-icons/fa'  
import {db} from '../firebase'
import './list.css'
import {Link} from 'react-router-dom'
import ListItem from './listItem';
var length;

  class ClassList extends React.Component{
    state = {
        classes:null,
        name:"null",
      }

      componentDidMount(){
        const data = db.collection('Users').doc(this.props.userId).collection('Classes');    
        const classes = []
         data.get()
          .then(snapshot=>{
            length = snapshot.size;
            snapshot.forEach(doc=>{
              const data = doc.data()
              classes.push(data)
            })
            this.setState({classes:classes})
          })
      }
     
      render(){
          return(
              <div>
                <div style={{backgroundColor:'#E6E6E6'}}>
                    <div class='number'><FaThumbsUp size='12' style={{marginRight:'5px'}}/> Found {length} Pidgin classes around you.</div>
                    {       
                              this.state.classes&&
                              this.state.classes.map(classes=>{
                                return(
                                 <ListItem classID={classes.id} uid={this.props.userId} />
                                )
                          })
                        }
                  </div>
            </div>
          )
      }
  }
  export default ClassList;